<template>
  <div class="modify-param-set">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="100"
      class="form-content"
    >
      <FormItem label="变量名称" prop="name">
        <Input v-model="formValidate.name" placeholder="请输入变量名称"></Input>
      </FormItem>
      <FormItem label="变量值" prop="busis">
        <Input v-model="formValidate.busis" placeholder="请输入变量值"></Input>
      </FormItem>
      <FormItem label="变量说明" prop="personer">
        <Input v-model="formValidate.personer" placeholder="请输入变量说明"></Input>
      </FormItem>
      <FormItem >
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  props: ["dataObj"],
  data() {
    return {
      formValidate: {
        name: "",
        busis: "",
        personer: "",
      },
      ruleValidate: {
        busis: [
          {
            required: true,
            message: "业务类型不能为空！",
            trigger: "change"
          }
        ],
        name: [
          {
            required: true,
            message: "业务代码不能为空！",
            trigger: "blur"
          }
        ],
        personer: [
          {
            required: true,
            message: "业务名称不能为空！",
            trigger: "blur"
          }
        ],
        department: [
          {
            required: true,
            message: "所属业务部门不能为空！",
            trigger: "change"
          }
        ],
        create_date: [
          {
            required: true,
            type: "date",
            message: "建立日期不能为空！",
            trigger: "change"
          }
        ],
        bus_describe: [
          {
            required: true,
            message: "业务描述不能为空！",
            trigger: "blur"
          },
          {
            type: "string",
            max: 512,
            message: "业务描述不能超过512个字！",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
    console.log("dataObj", this.dataObj);
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          console.log('this.formValidate', this.formValidate)
          this.$emit("save", this.formValidate);
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" @scoped>
.modify-param-set {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
  }
}
</style>


