<template>
  <div class="param-set">
    <div class="topTitle">
      <span class="topTitle-span1">系统管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">基础设置</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">参数设置</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody">
          <Form
            ref="formValidate"
            :model="formValidate"
            :rules="ruleValidate"
            :label-width="120"
            inline
          >
            <Row>
              <FormItem label="变量名称：" prop="Inputvalue1">
                <Input v-model="formValidate.Inputvalue1" placeholder="请输入变量名称" style="width:200px"></Input>
              </FormItem>
              <FormItem label="变量说明：" prop="Inputvalue1">
                <Input v-model="formValidate.Inputvalue1" placeholder="请输入变量说明" style="width:200px"></Input>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button
                  type="primary"
                  @click="handleSubmit('formValidate')"
                >查询</Button>
                <Button
                  type="primary"
                  @click="handleSubmit('formValidate')"
                >精确查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                  <td>{{index+1}}</td>
                  <td>
                    <span class="spanBox" @click="operate('2',index)">修改</span>
                  </td>
                  <td>{{item.name}}</td>
                  <td>{{item. busis}}</td>
                  <td>{{item.personer}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
      <div class="flexCenter pageBox">
        <Page :total="tableBody.length" :page-size="pageSize" @on-change="pageChange" show-elevator />
      </div>
    </div>
    <Modal :mask-closable="false"  
      v-model="showAddModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '60%'}"
    >
      <modifyParamSet @cancel="cancelDialog" @save="saveDatas" :dataObj="dataObj"  width="80%"></modifyParamSet>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import modifyParamSet from "./modifyParamSet"; //添加和修改
export default {
  components: {
    modifyParamSet
  },
  data() {
    return {
      showAddModal: false,
      dataObj:{
        type: "1", //1新增，2修改
        data:[],
      },
      dataIndex:undefined,
      modelTitle: "新增",
      rowCount: 0,
      pageSize: 2,
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "变量名称" },
        { name: "变量值" },
        { name: "变量说明" },
      ],
      tableBody: [
        {
          name: "A股",
          busis: "LOG_PATH",
          personer: "533",
        },
        {
          name: "A股",
          busis: "LOG_PATH",
          personer: "533",
        },
        {
          name: "A股",
          busis: "LOG_PATH",
          personer: "533",
        },
        {
          name: "A股",
          busis: "LOG_PATH",
          personer: "533",
        }
      ],
      formValidate: {
        Inputvalue1: "", //机构代码
        Inputvalue2: "" //机构名称
      },
      ruleValidate: {
        Inputvalue1: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ],
        Inputvalue2: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    delItem(){
        this.$Modal.confirm({
            title: "是否删除？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
            },
            onCancel: () => {
            }
        });
    },
    cancel() {},
    operate(type, index) {
      this.showAddModal = true;
      this.type = type;
      type==1?this.modelTitle='新增':this.modelTitle='修改';
      this.dataIndex = index;
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
        } else {
        }
      });
    },
    pageChange(pageNo) {
      let that = this;
      let params = new FormData();
      params.append("code", "list_app_fina");
      params.append("pageNo", pageNo);
    },
    cancelDialog(val) {
      this.showAddModal = val;
    },
    saveDatas(data){
      this.showAddModal = false;
      this.tableBody[this.dataIndex] = data;
      console.log('data', data)
    }
  }
};
</script>
<style lang="scss" @scoped>
    .param-set {
    .rightTopbtn {
        margin: 15px 4px;
        float: right;
    }
    }
</style>



